import { Component, OnInit, ViewChild } from '@angular/core';
import { VesselsService, Vessel } from "../../../generated/vessels/index";
import { Router } from "@angular/router";
import { HotTable } from 'ng2-handsontable';

@Component({
  selector: 'app-vessels',
  templateUrl: './vessels.component.html',
  styleUrls: ['./vessels.component.css'],
  providers: [VesselsService]
})

export class VesselsComponent implements OnInit {
  private ignoreKeyList :string[] = ['mmxId', 'version', 'versions', 'ladenAttributes', 'ballastAttributes']
  private colHeaders: string[];
  private rowHeaders: string[];
  private data: any[][] = [['a','a','a','a','a'], ['b','b','b','b','b']]; 
  private options: any = {
    rowHeaders: [1, 2, 3, 4, 5],
  }
  @ViewChild(HotTable) hotTable: HotTable;
  private tableOrientationToggle: boolean = true;

  ngOnInit(): void {
    this.getVessels();
  }

  constructor(
    private router: Router,
    private vesselsService: VesselsService
  ) {}
  vessels: Vessel[];

  tokenizeLabels(labels: string[]): string[] {
    return labels.map(k => k.split(/(?=[A-Z])/).map(token => token[0].toUpperCase() + token.substr(1, token.length)).join(' '));
  }

  emptyRow(rowHeaders: string[], data: any[], label = ''): void {
    rowHeaders.push(label);
    data.push('');
  }

  getVessels(): void {
    this.vesselsService.getVesselsUsingGET().toPromise()
    .then(vessels => { 
      this.vessels = vessels;
      this.refreshTable();
    })
    .catch(error => console.log(error));
  }
  
  refreshTable(): void {
    if (this.tableOrientationToggle) {
      this.createHorizontalTable(this.vessels);
    } else {
      this.createVerticalTable(this.vessels);
    } 
  }
  
  private specificFormatters : any = {"mmxId":      {"format": "", "type": "", "Unit": ""},
                             "name":                {"format": "", "type": "", "Unit": ""},
                             "imo":                 {"format": "", "type": "", "Unit": ""},
                             "inaccessiblePorts":   {"format": "", "type": "", "Unit": ""},
                             "inaccessibleRoutes":  {"format": "", "type": "", "Unit": ""},
                             "baseFuel":            {"format": "", "type": "", "Unit": ""},
                             "capacity":            {"format": "", "type": "", "Unit": "m3"},
                             "fillCapacity":        {"format": "", "type": "", "Unit": "%"},
                             "ladenAttributes":     {"format": "", "type": "", "Unit": ""},
                             "ballastAttributes":   {"format": "", "type": "", "Unit": ""},
                             "minSpeed":            {"format": "", "type": "", "Unit": "kts"},
                             "maxSpeed":            {"format": "", "type": "", "Unit": "kts"},
                             "minHeel":             {"format": "", "type": "", "Unit": "m3"},
                             "warmingTime":         {"format": "", "type": "", "Unit": "hrs"},
                             "coolingVolume":       {"format": "", "type": "", "Unit": "m3"},
                             "pilotLightRate":      {"format": "", "type": "", "Unit": "MT/day"},
                             "minBaseFuelConsumption": {"format": "", "type": "", "Unit": ""},
                             "hasReliqCapacity":       {"format": "", "type": "", "Unit": ""},
                             "scnt":                   {"format": "", "type": "", "Unit": ""},
                             "nboRate":                {"format": "", "type": "", "Unit": "m3/day"},
                             "idleNBORate":            {"format": "", "type": "", "Unit": "m3/day"},
                             "idleBaseRate":           {"format": "", "type": "", "Unit": "MT/day"},
                             "inPortBaseRate":         {"format": "", "type": "", "Unit": "MT/day"},
                             "serviceSped":            {"format": "", "type": "", "Unit": "kts"},
                             "inPortNBORate":          {"format": "", "type": "", "Unit": "m3/day"}};

  createHorizontalTable(vessels: Vessel[]): void {
    this.rowHeaders = [];
    this.colHeaders = [];
    this.data = [];

    let formatters: any[] = [];
    this.rowHeaders = vessels.map(v => v.mmxId);
    console.log(Object.keys(vessels[0]));
    let trimmedKeys: string[] = Object.keys(vessels[0])
    .filter(k => !this.ignoreKeyList.includes(k));

    this.data = vessels.map(vessel => trimmedKeys.map(key => vessel[key]));
    this.colHeaders = this.tokenizeLabels(trimmedKeys.map(key => 
      this.specificFormatters[key]["Unit"] === "" ? `${key}` : `${key} (${this.specificFormatters[key]["Unit"]})`));
    
    formatters.push(...trimmedKeys.map(key => {
      if (typeof vessels[0][key] === "number") {
        return ({'type':'numeric'})
      } else {
        return ({'type':'text'})
      }
    }));

    let ladenKeys: string[] = Object.keys(vessels[0].ladenAttributes);
    console.log(ladenKeys);
    this.colHeaders.push(...this.tokenizeLabels(ladenKeys));

    for (let i = 0, len = vessels.length; i < len; i++) {
      this.data[i].push(...ladenKeys.map(key => vessels[i]['ladenAttributes'][key]));
    }
    
    formatters.push(...ladenKeys.map(key => {
      if (typeof vessels[0]['ladenAttributes'][key] === "number") {
        return ({'type':'numeric'})
      } else {
        return ({'type':'text'})
      }
    }));

    let ballastKeys: string[] = Object.keys(vessels[0].ballastAttributes);
    this.colHeaders.push(...this.tokenizeLabels(ballastKeys));

    for (let i = 0, len = vessels.length; i < len; i++) {
      this.data[i].push(...ballastKeys.map(key => vessels[i]['ballastAttributes'][key]));
    }
    
    formatters.push(...ballastKeys.map(key => {
      if (typeof vessels[0]['ballastAttributes'][key] === "number") {
        return ({'type':'numeric'})
      } else {
        return ({'type':'text'})
      }
    }));

    this.options = {
      colHeaders: this.colHeaders,
      rowHeaders: this.rowHeaders,
      rowHeaderWidth: 200,
      columns: formatters,
      minRows: this.rowHeaders.length,
      minCols: this.colHeaders.length,
      manualColumnResize: true,
    }
  }

  createVerticalTable(vessels: Vessel[]): void {
    this.rowHeaders = [];
    this.colHeaders = [];
    this.data = [];

    this.colHeaders = vessels.map(v => v.mmxId);
    let trimmedKeys: string[] = Object.keys(vessels[0])
    .filter(k => !this.ignoreKeyList.includes(k))

    this.data = trimmedKeys.map(key => vessels.map(v => v[key]));
    this.rowHeaders = this.tokenizeLabels(trimmedKeys);

    let ladenKeys: string[] = Object.keys(vessels[0].ladenAttributes);
    this.rowHeaders.push(...this.tokenizeLabels(ladenKeys));
    this.data.push(...ladenKeys.map(key => vessels.map(v => v['ladenAttributes'][key])));

    let ballastKeys: string[] = Object.keys(vessels[0].ballastAttributes);
    this.rowHeaders.push(...this.tokenizeLabels(ballastKeys));
    this.data.push(...ballastKeys.map(key => vessels.map(v => v['ballastAttributes'][key])));

    this.options = {
      colHeaders: this.colHeaders,
      rowHeaders: this.rowHeaders,
      rowHeaderWidth: 200,
      columns: Array.apply(null, {length: this.colHeaders.length}).map(i => ({'type':'text'})),
      minRows: this.rowHeaders.length,
      minCols: this.colHeaders.length,
      data: this.data
    }  
  }
  
  stitchSpreadsheetData(values: any[], keys: string[]) {
    let dataObject = keys.map((k, i) => ({k: values[i]}))
    let result = Object.assign({}, ...dataObject);

    return result;
  }

  gotoDetail(selectedVessel: Vessel): void {
    this.router.navigate(['../vessel', selectedVessel.mmxId, 0 ]);
  }

  deleteVessel(selectedVessel: Vessel): void {
    console.log("Deleted: " + selectedVessel.mmxId)
    this.router.navigate(['../remove', selectedVessel.mmxId]);
  }

  toggleTableOrientation(): void {
    this.tableOrientationToggle = !this.tableOrientationToggle;
    this.refreshTable();
  }

  addVessel(): void {
    this.router.navigate(['../vessel', '', '']);
  }

}



